import {Article} from "./Article";

export function About() {
    return (
        <Article title={'O Mn'}>
            <img className="text-start" src="https://via.placeholder.com/720x960" alt="profilov fotka" style={{
                float:'right',
                margin: '1rem',
                width: '15rem',
            }}/>
            <p>

                Narodila jsem se 27.10.1969 v Plzni a dtstv strvila v Tachov.
                Vdy jsem inklinovala k humanitnm oborm a umn. Od maminky a babiky jsem okoukala hodn o
                bylinkch, jejich vyuit a pomoci v bnm ivot.
            </p>
            <p>
                Pak nsledovalo studium maturitnho oboru Kosmetika, vizistka v Hradci Krlov, prce
                Kosmetiky v Praze, tlet pobyt v Kanad, kde jsem zskala praxi. Tak se mi narodil mj prvn syn.
                Poteba rozit vdomosti v celostnm smyslu m pivedla ke kole aromaterapie, kde jsem se
                seznmila s energetickmi systmy v tle, zklady akupresury, rznmi druhy mas a nauila se
                vyrobit prakticky vechny kosmetick ppravky a liv tinktury. S tm lo ruku v ruce studium
                psychologie, zdravho stravovn a cvien, hlavn pi problmech s pte. Absolvovala jsem dva
                stupn Reiki. Dky znalosti anglitiny jsem mla monost erpat i z ciz literatury. Po narozen druhho
                syna jsem absolvovala kurz osobnho rozvoje u Stars edge international a zskala zde certifikt mistra
                vuky.
            </p>
            <p>
                Vlastn praxi jsem provozovala v Tachov a zanedlouho pibyla i vuka anglitiny a po narozen dcery
                studium pedagogiky ve Svatm Janu pod Skalou. K tomu jsem ve svm volnu aktivn zpvala a
                vyrbla keramiku. Ta m oslovila natolik, e dal kurz byla rekvalifikace v oboru Umleck hrn,
                to a objeven arteterapie.
                Dti u jsou z domu a ivot m pivedl na vysnn msto, do Konstantinovch Lzn. Tady, v krsn
                prod mohu sbrat bylinky, zskvat elezo pro dekoraci keramiky a provozovat ve, co m bav a
                miluji.
            </p>
        </Article>
    );
}